@import "../../sass";

.container {
    padding-top: 100px;
    background-color: $_br_viewsPage;
    .list_item {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        list-style: none;
        padding: 20px;
        .item {
            margin-bottom: 70px; 
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            position: relative;
            border-radius: 5px;
            overflow: hidden;
            box-shadow: 3px 3px 6px rgba(0, 0, 0, 0.6);
            width: 477px;
            .item_top {
                .link_item_top {
                    overflow: hidden;
                    &:hover .item_img {
                        transform: scale(1.08);
                    }
                    display: flex;
                    .item_img {
                        width: 100%;
                        transition: 1s cubic-bezier(0.175, 0.885, 0.32, 1.275);
                    }
                }
            }
            .item_info {
                display: flex;
                margin-top: 10px;
                .item_info_left {
                    width: 60%;
                    display: flex;
                    flex-direction: column;
                    & a {
                        display: block;
                        padding: 8px;
                        color: $_222;
                        text-decoration: none;
                    }
                    .item_cc {
                        font-weight: bold;
                    }
                    .item_name {
                        font-weight: bold;
                        &:hover {
                            transition: all 0.6s ease;
                            text-shadow: 2px 3px 1px rgb(0 0 0 / 41%);
                        }
                        font-size: 1rem;
                        padding-left: 6px;
                        position: relative;
                        &::after {
                            @include absolute(auto, 10%, 2px, 6px);
                            height: 2px;
                            background-color: $_border_primary;
                        }
                    }
                }
                .item_price {
                    padding-left: 8px;
                    margin: 6px 0;
                }
                .item_info_right {
                    width: 40%;
                    display: flex;
                    align-items: start;
                    .moreInfoMoto {
                        margin-left: 4px;
                        cursor: pointer;
                        padding: 10px;
                        background-color: $_000;
                        color: $_fff;
                        font-weight: bold;
                        border-bottom-left-radius: 14px;
                        border-top-right-radius: 14px;
                        border: 1px solid $_border_primary;
                        transition: .3s linear;
                        &:hover {
                            border-bottom-right-radius: 14px;
                            border-top-left-radius: 14px;
                            border-bottom-left-radius: 0;
                            border-top-right-radius: 0;
                            background-color: $_fff;
                            color: $_000;
                        }
                    }
                }
            }
        }
    }
}